.main-nav{
  padding: 25px 30px;
  @include desktop{
    background: $primary-color;
    box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.09);
    padding: 20px;
  }
  .navbar-nav{
    @include desktop{
      text-align: center;
    }
    .nav-item{
      .nav-link{
        color: $white;
        padding-left: 20px;
        padding-right: 20px;
        font-size: 15px;
        text-transform: capitalize;
        letter-spacing: 1px;
        position: relative;
        z-index: 1;
        font-family: $primary-font;
        position: relative;
        @include desktop{
          padding: 15px 0;
        }
        &:before{
          position: absolute;
          content: '';
          width: 20px;
          height: 3px;
          z-index: -1;
          background: $white;
          left: 21px;
          bottom: 5px;
          border-radius: 3px;
          transform: scaleX(0);
          transform-origin: right;
          transition: transform .3s ease;
          @include desktop{
            content: none;
          }
        }
        &:hover{
          &:before{
            transform: scaleX(1);
            transform-origin: left;
          }
        }
        &.active{
          &:before{
            transform: scaleX(1);
            transform-origin: left;
          }
        }
      }
    }
    @include desktop{
      margin-bottom: 20px;
    }
  }
  .nav-signin{
    margin-left: 20px;
    @include desktop{
      text-align: center;
      margin-left: 0;
    }
  }
  .navbar-brand{
    margin-right: 0;
    text-align: center;
    img{
      max-width: 150px;
    }
  }
  .navbar-toggler{
    outline: 0;
    color: $white;
    cursor: pointer;
  }
  &.nav-bg{
    background: $primary-color;
    padding: 20px;
    border: none;
    box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.09);
    @include desktop{
      padding: 15px 20px;
    }
  }
}

// Headroomjs Styles
.headroom {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transition: all .5s ease-in-out;
}
.headroom--unpinned {
  top: -100px !important;
  @include desktop{
    top: 0 !important;
  }
}
.headroom--pinned {
  top: 0;
}


.nav{
  &-offcanvas{
    width: 300px;
    background: linear-gradient( 108deg, rgb(255,142,127) 0%, rgb(255,87,154) 100%);
    height: 100vh;
    position: fixed;
    overflow-x:auto;
    z-index: 2000;
    padding: 50px 0;
    top: 0;
    right: -300px;
    transition: .3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    .close{
      position: absolute;
      right: 10px; top: 20px;
      outline: 0;
      opacity: 1;
      background: transparent;
      border: none;
      box-shadow: none;
      cursor: pointer;
      transition: .3s ease;
      i{
        font-size: 15px;
        color: white;
        text-shadow: none;
      }
      &:hover{
        transform: rotate(90deg);
      }
    }
    &-menu{
      ul{
          margin-bottom: 0;
          padding-left: 0;
          list-style: none;
          li{
            a{
              color: white;
              font-size: 16px;
              font-weight: 300;
              padding: 10px 40px;
              display: block;
              text-decoration: none;
              position: relative;
              z-index: 1;
              transition: .3s cubic-bezier(0.25, 0.46, 0.45, 0.94) .05s;
              font-family: $primary-font;
              font-size: 24px;
            }
          }
      }
      .nav-signin{
        width: 80%;
        position: absolute;
        bottom: 70px;
        left: 10%;
      }
    }
    &.open{
      right: 0;
    }
  }
}

.offcanvas-overlay{
  position: fixed;
  top: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, .7);
  z-index: 1999;
  transition: .3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  visibility: hidden;
  opacity: 0;
  &.on{
    opacity: 1;
    visibility: visible;
  }
}