.btn-white{
  background: $white;
  color: $primary-color;
  padding: 20px 50px;
  border-radius: 50px;
  font-family: $primary-font;
  transition: .3s ease;
  @include mobile{
    padding: 15px 30px;
  }
  &:hover{
    background: $primary-color;
    color: $white;
    transform: scale(1.01);
    box-shadow: 0px 70px 70px 0px rgba(25, 28, 33, 0.1);
  }
}

.btn-secondary{
  background-image: linear-gradient( 135deg, rgb(62,27,189) 0%, rgb(160,97,255) 100%);
  box-shadow: 0px 70px 70px 0px rgba(25, 28, 33, 0.1);
  padding: 20px 70px;
  border-radius: 50px;
  font-size: 18px;
  font-family: $primary-font;
  @include mobile{
    padding: 15px 40px;
  }
}

.btn-nav{
  color: $white;
  display: flex;
  align-items: center;
  background: rgba($color: $white, $alpha: .26);
  padding: 10px 30px;
  border-radius: 30px;
  span{
    font-family: $primary-font;
  }
  span, i{
    display: inline-block;
  }
  i{
    margin-left: 20px;
    font-size: 35px;
  }
  @include desktop{
    justify-content: center;
  }
  &:hover{
    background: $white;
    color: $primary-color;
  }
}